export default function WhyChooseUs() {
  const cardRef1 = useRef(null);
  const cardRef2 = useRef(null);

  useEffect(() => {
    const handleScroll = () => {
      [cardRef1, cardRef2].forEach(ref => {
        if (ref.current) {
          const rect = ref.current.getBoundingClientRect();
          if (rect.top < window.innerHeight - 100) {
            ref.current.classList.add("animate-fadeup");
          }
        }
      });
    };
    window.addEventListener("scroll", handleScroll);
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="relative py-16 px-4 md:px-0 bg-gradient-to-br from-white via-gray-50 to-gray-100">
      <h2 className="text-3xl md:text-4xl font-serif font-bold text-center mb-12 tracking-tight text-gray-900">Why Choose Us</h2>
      <div className="flex flex-col md:flex-row md:justify-center gap-8 items-stretch max-w-3xl mx-auto">
        <div
          ref={cardRef1}
          className="opacity-0 transform scale-95 transition-all duration-700 ease-out bg-white rounded-2xl shadow-lg flex-1 p-8 flex flex-col items-center justify-center"
        >
          <FaStar className="text-yellow-500 text-5xl mb-4" />
          <div className="font-serif font-semibold text-xl mb-2">Quality Products</div>
          <div className="text-gray-600 text-base text-center">We sell good quality products</div>
        </div>
        <div className="hidden md:flex items-center">
          <div className="w-px h-24 bg-gradient-to-b from-gray-200 via-gray-400 to-gray-200 mx-4" />
        </div>
        <div
          ref={cardRef2}
          className="opacity-0 transform scale-95 transition-all duration-700 ease-out bg-white rounded-2xl shadow-lg flex-1 p-8 flex flex-col items-center justify-center"
        >
          <FaHandshake className="text-blue-500 text-5xl mb-4" />
          <div className="font-serif font-semibold text-xl mb-2">Distributor Opportunity</div>
          <div className="text-gray-600 text-base text-center">We offer the stock price for distributors who want to start their own business</div>
        </div>
      </div>
      <style jsx>{`
        .animate-fadeup {
          opacity: 1 !important;
          transform: scale(1) translateY(0) !important;
        }
      `}</style>
    </section>
  );
}
